
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


export default {
name: "Header",
components: {
Swiper,
SwiperItem
},
data() {
return {
baseUrl: "http://127.0.0.1:8000/collection/get_head_article/",

headerindex: 0,
headerList: [],
baseList: []
};
},
mounted() {
this.$nextTick(function() {


this.getHeadArt();
});
},
methods: {
getHeadArt() {
this.$http({
method: "GET",
url: this.baseUrl,
headers: {


"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
}
}).then(response => {
this.baseList = response.data;
});
}
}
};



